import React from "react";
import styled from "styled-components";
import Img from "../Image/loading1.gif";

const LoadingPage = () => {
  return (
    <Section>
      <Container>
        <Image src={Img} alt="loading..." />
      </Container>
    </Section>
  );
};

export default LoadingPage;

export const Section = styled.section({
  width: "100%",
  height: "100vh",
  position: "absolute",
  top: 0,
  left: 0,
  right: 0,
  bottom: 0,
  background: "transparent",
  display: "flex",
  alignItems: "center",
  justifyContent: "center",
});

export const Container = styled.div({
  width: "350px",
  height: "200px",
  borderRadius: "5px",
  display: "flex",
  alignItems: "center",
  justifyContent: "center",
  background: "#E5EFF1",
});

export const Image = styled.img({
  height: "50%",
  width: "auto",
});
