import "./App.css";
import { BrowserRouter, Route, Routes, Navigate } from "react-router-dom";
import { lazy, Suspense } from "react";
import { useSelector } from "react-redux";
import LoadingPage from "./components/LoadingPage";

const Login = lazy(() => import("./pages/Login"));
const Home = lazy(() => import("./pages/Home"));
const EmployeePage = lazy(() => import("./pages/EmployeePage"));
const PageNotFound = lazy(() => import("./pages/PageNotFound"));
const RequireAuth = lazy(() => import("./utils/RequireAuth"));
const Navbar = lazy(() => import("../src/components/NavigationBar"));
const Footer = lazy(() => import("../src/components/NavigationBar/Footer"));

function App() {
  const { login } = useSelector((state) => state.login);
  return (
    <BrowserRouter basename="check-visa-expiry-limit">
      <Suspense fallback={<LoadingPage />}>
        {login && <Navbar />}
        <Routes>
          <Route path="/" element={<Login />} />
          <Route
            path="dashboard"
            element={
              <RequireAuth>
                <Home />
              </RequireAuth>
            }
          />
          <Route
            path="employee"
            element={
              <RequireAuth>
                <EmployeePage />
              </RequireAuth>
            }
          />
          <Route path="*" element={<PageNotFound />} />
          <Route render={() => <Navigate to="/" />} />
        </Routes>
        <Footer />
      </Suspense>
    </BrowserRouter>
  );
}

export default App;
