import { createSlice } from "@reduxjs/toolkit";

export const tableSlice = createSlice({
  name: "table",
  initialState: {
    tablePage: 1,
    displayRowsLowestIndex: 0,
    displayRowsHighestIndex: 10,
  },
  reducers: {
    increment: (state, action) => {
      state.displayRowsLowestIndex = action.payload;

      state.tablePage = state.tablePage + 1;
    },
    decrement: (state, action) => {
      if (state.tablePage > 1) {
        state.displayRowsLowestIndex = action.payload;
        state.tablePage = state.tablePage - 1;
      }
    },
    setTablePage: (state, action) => {
      state.tablePage = action.payload;
    },
    setLowestIndex: (state, action) => {
      state.displayRowsLowestIndex = action.payload;
    },
    reset: (state) => {
      state.tablePage = 1;
      state.displayRowsLowestIndex = 0;
    },
  },
});

export const { increment, decrement, reset, setTablePage, setLowestIndex } =
  tableSlice.actions;

export default tableSlice.reducer;
